<template>
    <div class="app">
        <post-form @create="createPost" />
        <post-list v-bind:posts="posts" /> <!-- можно просто :posts -->
    </div>
</template>

<script>
import PostForm from '@/components/PostForm' //@ означает папку src
import PostList from './components/PostList'
export default {
    components: {
        PostForm, PostList
    },

    data() {
        return {
            posts: [
                { id: 1, title: 'JavaScript', body: 'jQuery or no?' },
                { id: 2, title: 'Java', body: 'Spring or no?' },
                { id: 3, title: 'Go', body: 'Golang or WSCode?' },
            ]
        }
    },

    methods: {
        createPost(post) {
            this.posts.push(post)
        }
    }
}
</script>

<style>
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.app {
    padding: 20px;
}
</style>