<template>
    <form @submit.prevent>
            <h4>Create new post</h4>
            <input-post
                v-bind:value="post.title"
                @input="post.title = $event.target.value"
                type="text"
                placeholder="Name">
            </input-post>
            <!-- через директиву v-model -->
            <input-post
                v-model="post.body"
                type="text"
                placeholder="Description">
            </input-post>
            <button-post
                style="align-self: flex-end; margin-top: 15px"
                @click="createPost"
            >Create</button-post>
        </form>
</template>

<script>
import ButtonPost from '@/components/UI/ButtonPost'
export default {
    components: {
        ButtonPost
    },

    data() {
        return {
            post: {
                title: '',
                body: ''
            }
        }
    },

    methods: {
        createPost() {
            this.post.id = Date.now()
            this.$emit('create', this.post)
            //clear fields
            this.post = {
                title: '',
                body: ''
            }
        }
    }
}
</script>

<style scoped>

form {
    display: flex;
    flex-direction: column;
}

</style>